@import "./varibles.scss";
@import "./callout.scss";

body{
  background-color: #f0f0f0;
  font-family: 'Roboto', sans-serif;
}

.row{
  margin: 0;
}

a {
  text-decoration: none;
}

h1 {
  font-family: $open-sans;
  font-weight: 300;
}






// @media #{$xs}{}


// Responsive Variables



/*------------------- Responsive --------------------------*/







.text-left{
  text-align: left!important;
}

// Theme section color
.color1{
	background: #ffe7e6;
}
.color2{
	background: #f4ffd5;
}
.color3{
	background: #e1fcff;
}
.color4{
	background: #fff9c6;
}

.p-t-10{
  padding-top: 10px!important;
}
.p-m-10{
  padding-bottom: 10px!important;
}
// pading && margin
.mt-10 {
  margin-top: 10px;
}
.m-b-0{
margin-bottom: 0!important;
}
.m-t-50{
	margin-top: 50px!important;
}
.p-10{
	padding: 10px !important;
}

.mb-30 {
  margin-bottom: 30px;
}
.mt-30 {
  margin-top: 30px;
}

.section-top-border {
  padding: 50px 0;
  border-top: 1px dotted #eee;
}

.top-bg{
	background:#0000;
}

.white-bg{
	background: #ffffff;
}
.gray-bg{
	background: #f5f5f5;
}


.left-card >div{
	border-bottom: 1px solid rgb(228, 222, 222);
}




/*-------------Color include--------------*/


/*-- Background color---*/

.gray-bg {
	background: $gray;
}
.white-bg {
	background:$white;
}
.black-bg {
	background: $black;
}
.theme-bg {
	background:$theme-color;
}
.brand-bg{
	background: $brand-bg;
}
.testimonial-bg{
	background: $testimonial-bg;
}


/*--- color------*/
.white-color {
	color: $white;
}
.black-color {
	color: $black;
}
.theme-color {
	color: $theme-color;
}

.w100{
	width: 100%!important;
}


// card large style
.leftbar-style{
	margin-top: 15px!important;
	padding-bottom: 10px;
	border-bottom: 1px solid #b0b9c4b2;
	a{
		color: #020d1ac4;
	}
	&>div{
		margin-bottom: 20px;
	}
	h3{
		border-bottom: 1px solid rgb(139, 135, 135);
		padding-bottom: 10px;
	}
}


// for label
.ant-form-rtl .ant-form-item-label{
	text-align: right!important;
}

// popover menu
.ant-popover-inner-content {
    padding: 0!important;
}